<template>
  <div class="homepage">
    <NavBar></NavBar>
    <!--        轮播图-->
    <el-carousel height="460px" class="carousel">
      <el-carousel-item v-for="(item, index) in carouselImgs" :key="index">
        <img :src="item.url" alt="" style="width: 1500px" />
      </el-carousel-item>
    </el-carousel>

    <el-row :gutter="30" style="margin-left: 9%; margin-right: 9%">
      <el-col v-if="entities.length == 0" style=""
        ><el-empty description="暂无数据"></el-empty
      ></el-col>
      <el-col
        v-else
        :span="8"
        v-for="(o, index) in entities"
        :key="index"
        class="card-col"
      >
        <el-card style="border-radius: 25px">
          <el-image :src="demoImgUrl">
            <div slot="error" class="image-slot">
              <i class="el-icon-picture-outline"></i>
            </div>
          </el-image>
          <div style="padding: 10px">
            <div style="text-align: left; overflow: hidden">
              <h4>DID:<span class="asset-discription">{{ o.did }}</span></h4>
              <h4>ID:<span class="asset-discription">{{ o.id }}</span></h4>
              <h4>Name:<span class="asset-discription">{{ o.name }}</span></h4>
              <h4>Owner:<span class="asset-discription">{{ o.owner }}</span></h4>
              <h4>Price:<span class="asset-discription">{{ o.price ? o.price : "N/A" }}</span></h4>
              <h4>IPFShash:<span class="asset-discription">{{ o.ipfs }}</span></h4>
            </div>
            <div class="bottom clearfix">
              <el-button
                type="success"
                class="button"
                @click="getIPFS(o.ipfs)"
                icon="el-icon-search"
                >IPFS</el-button
              >
              <el-button type="primary" class="button" @click="buyAsset(o.id)"
                >购买它</el-button
              >
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <Footer></Footer>
  </div>
</template>

<script>
import NavBar from "../components/NavBar";
import Footer from "../components/Footer";
import { getAsset } from "../graphql/graphql";
import axios from "axios";
import Web3 from "web3";
export default {
  name: "Macketpalce",
  data() {
    return {
      carouselImgs: [
        {
          caption: "carouse-2",
          url: require("../assets/images/carousel-2.jpg"),
        },
        {
          caption: "carouse-3",
          url: require("../assets/images/carousel-3.jpg"),
        },
        {
          caption: "carouse-4",
          url: require("../assets/images/carousel-4.jpg"),
        },
      ],
      entities: [
        {
          did: "did:dns:example.com",
          id: "0x0698a219f013cd9d3b6a5c2b6f633d4ae07282cf",
          name: "example.com domain",
          owner: "0x8db58193c03fe7e5d2d2ab9bf1c80f847c706743",
          price: null,
          url: "https://dam.whh.pw//api/asset/valid?id=example.com",
          ipfs: "QmTaq2X243Wn3vAydoEjPHeFccLBYGYR9LEuYcnN7CQDKv",
        },
      ],
      msg: "enable metamask",
      currentDate: new Date(),
      demoImgUrl:
        "https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fp5.itc.cn%2Fimages01%2F20210506%2Fa8181137567e4fe99e49cf34fc968387.jpeg&refer=http%3A%2F%2Fp5.itc.cn&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=auto?sec=1651634552&t=a3d21b8e08faa20460acac5311874469",
    };
  },
  components: {
    NavBar,
    Footer,
  },
  methods: {
    checkmetamask() {
      const { ethereum } = window;
      if (ethereum && ethereum.isMetaMask) {
        //If it isn't installed we ask the user to click to install it
        console.log("connect");
        this.msg = "Connect";
        ethereum.request({ method: "eth_requestAccounts" });
      } else {
        //If it is installed we change our button text
        this.msg = "Click here to install MetaMask!";
      }
    },
    buyAsset(address) {
      const { ethereum } = window;
      if (ethereum && ethereum.isMetaMask) {
        //If it isn't installed we ask the user to click to install it
        console.log("connect");
        this.msg = "Connect";
        ethereum.request({ method: "eth_requestAccounts" });

        axios.get("https://whh.buzz/AssetDemo.json").then((res) => {
          console.log(address);
          console.log(res.data);
          // abi = JSON.parse(res.data);
          var abi = res.data;
          console.log(abi);
          const web3 = new Web3(ethereum);
          const assetContract = new web3.eth.Contract(abi, address);
          console.log(abi);
          var options = {
            from: ethereum.selectedAddress,
          };
          assetContract.methods
            .buy()
            .send(options)
            .then((res) =>
              this.$message({
                message: JSON.parse(res),
                showClose: true,
                duration: 1000,
              })
            )
            .catch((err) => {
              this.$message.error(err);
            });
        });
      } else {
        //If it is installed we change our button text
        this.msg = "Click here to install MetaMask!";
      }
    },
    getIPFS(hash) {
      var ipfsAddr = "https://ipfs.io/ipfs/" + hash;
      console.log(ipfsAddr);
      window.open(ipfsAddr);
    },
  },
  mounted: function () {
    getAsset().then((res) => {
      console.log(res);
      this.entities = res.data.assetEntities;
    });
  },
};
</script>

<style scoped>

.asset-discription{
  color: rgb(100, 159, 63);
}
.asset-discription:hover{
  color: rgb(160, 65, 15);
}

.carousel {
  margin: 30px;
  margin-left: 10%;
  margin-right: 10%;
  border-radius: 30px;
}
.card-row {
  margin-left: 30px;
  margin-right: 30px;
}
.card-col {
  margin-top: 10px;
  margin-bottom: 10px;
}
</style>