import Vue from 'vue'
import VueRouter from 'vue-router'
import Marketplace from '../views/marketplace'
import AssetPublish from '../views/AssetPublish'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name:'marketpalce',
        alias:'/marketplace',
        component:Marketplace,
    }, 
    {
        name:'assetpublish',
        path:'/assetpublish',
        component:AssetPublish,
    }
]

const router = new VueRouter({
    routes
})

export default router
