<template>
  <div class="nav-header">
    <el-menu
      :default-active="this.getActiveIndex"
      class="el-menu-demo"
      mode="horizontal"
      @select="handleSelect"
      background-color="#fdfbfb"
      text-color="#000"
      active-text-color="#000"
      router
    >
      <el-menu-item index="/">
        <img
          src="../assets/images/wormholeLogo.png"
          alt=""
          style="height: 55px; width: 55px"
        />
        DID-Asseets
      </el-menu-item>
      <el-menu-item index="/marketplace">MarketPlace</el-menu-item>
      <el-menu-item index="/assetpublish">AssetPublish</el-menu-item>
      <el-button
        round
        type="info"
        disabled
        style="margin-top: 8px; float: right"
        v-if="metamaskEnabled()"
      >已连接MetaMask
      </el-button>
      <el-button
        round
        type="primary"
        @click="enableMetamask"
        style="margin-top: 8px; float: right"
        v-else
      >连接MetaMask
      </el-button>
    </el-menu>
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";

export default {
  data() {
    return {};
  },
  computed: {
    ...mapState(["activeIndex"]),
    ...mapGetters(["getActiveIndex"]),
  },
  methods: {
    metamaskEnabled() {
      const { ethereum } = window;
      return ethereum && ethereum.isMetaMask;
    },
    enableMetamask() {
        if(!this.metamaskEnabled()){
            this.$message.error("您的浏览器暂未安装metamask插件")
            return
        }
      const { ethereum } = window;
      ethereum.request({ method: "eth_requestAccounts" });
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
      if (key == "/" || key == "#") {
        this.$store.commit("updateActiveIndex", "/index");
      } else {
        this.$store.commit("updateActiveIndex", key);
      }
    },
  },
};
</script>
<style scoped>
.loginInput {
  margin: 0 0 20px 0;
}

.signInInput {
  margin: 0 0 20px 0;
}

.dropdown-style {
  margin-left: 60px;
}

.userInfo {
  /*margin-left: 150px;*/
  text-align: left;
}
</style>
