import ApolloClient from 'apollo-boost';
import gql from 'graphql-tag'
const apolloClient = new ApolloClient({
    uri: 'https://api.thegraph.com/subgraphs/name/jntmkk/dam3'
})

const Entities = `
    query MyQuery {
        assetEntities {
            did
            id
            name
            owner
            price
            url
            ipfs
        }
    }
`

export function getAsset() {
    return apolloClient
        .query({
            query: gql(Entities),
        })
}