import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        userName: '',
        activeIndex: '/marketplace',
        subPath: '/api', //可删除
    },
    mutations: {
        //更新用户信息
        updateUserInfo(state, userName) {
            state.userName = userName;
        },
        updateActiveIndex(state, index) {
            state.activeIndex = index;
        },
    },
    getters: {
        getUserName: (state) => {
            return state.userName ? state.userName : sessionStorage.getItem('userName')
        },
        getActiveIndex: (state) => {
            return state.activeIndex ? state.activeIndex : sessionStorage.getItem('activeIndex')
        },
    },
})
